import React from "react";
import { AiOutlineDoubleRight } from "react-icons/ai"

const Feature = ({ children }) => {

    return (
        <div className="feature">

            <AiOutlineDoubleRight className="icon" />

            <div className="text">
                {
                    children
                }
            </div>

        </div>
    )
}

export default Feature;